import './Portfolio.scss'
import {React, useEffect, useState} from 'react'
import PortfolioList from '../PortfolioList/PortfolioList'

import {
  feature,
  webDev,
  appDev,
  design, 
  tuition
} from '../../data'

function Portfolio({title}) {
  const [selected, setSelected] = useState('feature')
  const [data, setData] = useState([])

    const list = [
    {
      id: 'feature',
      title: 'Feature'
    },
     {
      id: 'web',
      title: 'Web Dev'
    },
     {
      id: 'app',
      title: 'App Dev'
    },
     {
      id: 'design',
      title: 'Design'
    },
     {
      id: 'tuition',
      title: 'Tuition'
    }
  ]

   useEffect(()=>{
     switch(selected){
         case 'feature': setData(feature)
         break
         case 'web': setData(webDev)
         break
         case 'mobile': setData(appDev)
         break
         case 'design': setData(design)
         break
         case 'branding': setData(tuition)
         break
         default:
           setData(feature)
     }
   },[selected])

  return (
    <div className='portfolio container' id='portfolio'>
      <div className="top">
        <h3>MY WORKS</h3>
        <h1 className="heading">Portfolio</h1>
      </div>
    <div className="bottom">
    <ul>
     {list.map(item=>
      <PortfolioList id={item.id} key={item.id}
      title={item.title} active={selected === item.id} setSelected={setSelected}/>
      )}
    </ul>
    <div className="content">
      {data.map(d=>
        <div key={d.id} className="item">
         <img src={d.img} alt="" />
         <h3>{d.title}</h3>
       </div>
        )}
    </div>
    </div>
    </div>
  )
}

export default Portfolio