import './Testimonial.scss'
import {ArrowBack, ArrowForward, Star} from '@material-ui/icons'
import test from '../../Assets/test.png'
import { useState } from 'react'




function Testimonial() {
  const [currentSlide, setCurrentSlide] = useState(0)

  
 const data = [
  {
    id: 1,
    person: 'James Antwi',
    title: 'Web Designer',
    info: 'Lorem ipsum, MSC dolor sit amet consectetur adipisicing elit.Cum molestiae architecto provident tempore nisi illo consequuntur, nesciunt natus! Quis, perspiciatis!',

  }
 ]
  return (
    <div className='testimonial container' id='testimonial'>
      <div className="top">
        <h3>WHAT CLIENTS SAY</h3>
        <h1 className="heading">Testimonial</h1>
      </div>
      <div className="bottom" style={{transform:`translateX(-${currentSlide * 50}vw)`}}>
        
        <div  className="left">
          {data.map(d=>
          <div key={d.id} className="content">
          <div className="star">
           <span><Star /></span>
           <span><Star /></span>
           <span><Star/></span>
           <span><Star/></span>
          </div>
          <p>{d.info}</p>
             <h3>{d.person}</h3>
             <h2>{d.title}</h2>
             </div>
               )}
             <div className="arrow">
              <ArrowBack/>
              <ArrowForward/>
             </div>
        </div>
       
        <div className="right">
          <img src={test} alt="" />
        </div>
      </div>
    </div>
  )
}

export default Testimonial