import './Contact.scss'
import {LocationOn, Send, PhoneIphone} from '@material-ui/icons'
import { useState } from 'react'

function Contact() {
const [person, setPerson] = useState({fullName:'', email:'', number:'', message:''})
const [people, setPeople] = useState([])



  const handleSubmit =(e) =>{
    e.preventDefault()
    if(person.fullName && person.email && person.number && person.message){
      const newPerson = {...person, id: new Date().getTime().toString()}
      setPeople([...people, newPerson])
      setPerson({fullName:'', email:'', number:'', message:''})
    }
  }
  
    const handleChange =(e) =>{
    const name = e.target.name
    const value = e.target.value
    setPerson({...person, [name]:value})
    console.log(name, value)
    
  }
  
  return (
    <>
    <div className='contact container' id='contact'>
      <div className="left">
        <div className="contact-top">
          <h1 className='heading'>Get In Touch</h1>
          <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. 
            Dolorem animi magnam quaerat? Fugiat ducimus natus
             eveniet quaerat distinctio dolor itaque!</p>
      
        </div>
        <div className="bottom">
          <p><span><LocationOn/></span>233 Kote Street, Sowutuom</p>
          <p><span><Send/></span>kode01@gmail.com</p>
          <p><span><PhoneIphone/></span>+233 (0)242 238 071</p>
        </div>
      </div>
      <div className="right">
        <h2>Fill this form</h2>
        <form action="" onSubmit={handleSubmit}>
          <input type="text" name='fullName' id='fullName' value={person.fullName} placeholder='Your name...'  onChange={handleChange}/>
          <input type="text" name='email' id='email' value={person.email} placeholder='Your email...' onChange={handleChange} />
          <input type="text" name='number' id='number' value={person.number} placeholder='Your phone number...'  onChange={handleChange}/>
          <textarea name="message" id="message" value={person.message} placeholder='Your message...' cols="30" rows="4" onChange={handleChange}></textarea>
          <input type="submit" value='SUBMIT' />
        </form>
        {/* {people.map(p=>
          <div key={p.id} className='output'>
            <p>{p.name}</p>
            <p>{p.email}</p>
             <p>{p.number}</p>
             <p>{p.message}</p>
          </div>
        )} */}
      </div>
    </div>
    </>
  )
}

export default Contact