import './Topbar.scss'

function Topbar({menuOpen, setMenuOpen}) {
  return (
    <div className={'topbar container '+ (menuOpen && 'active')}>
        <nav className='navbar'>
            <a className='logo' href="#home">kOde+</a>

            <div className="right">
            <ul>
                <li>
                    <a href="#home" onClick={()=>setMenuOpen(!menuOpen)}>Home</a>
                </li>
                 <li>
                    <a href="#services" onClick={()=>setMenuOpen(!menuOpen)}>services</a>
                </li>
                 <li>
                    <a href="#portfolio" onClick={()=>setMenuOpen(!menuOpen)}>portfolio</a>
                </li>
                 <li>
                    <a href="#about" onClick={()=>setMenuOpen(!menuOpen)}>about</a>
                </li>
                 <li>
                    <a href="#testimonial" onClick={()=>setMenuOpen(!menuOpen)}>testimonial</a>
                </li>
                 <li>
                    <a href="#contact" onClick={()=>setMenuOpen(!menuOpen)}>contact</a>
                </li>
                 <li>
                    <a href="#hireMe" onClick={()=>setMenuOpen(!menuOpen)}>hire me</a>
                </li>
            </ul>

            <div className="hamburger" onClick={()=>setMenuOpen(!menuOpen)}>
                <span className="line1"></span>
                <span className="line2"></span>
                <span className="line3"></span>
            </div>
            </div>
        </nav>

    </div>
  )
}

export default Topbar