import { useState } from 'react'
import './Clients.scss'
import ClientsList from '../ClientsList/ClientsList'

function Clients({title, info}) {
  const [selected, setSelected] = useState(1)
  

  const data = [
          {
            id:1,
            title:'500',
            info: 'Projects'
          },
           {
            id:2,
            title:'1500',
            info: 'Happy Clients'
          },
           {
            id:3,
            title:'4500',
            info: 'Work Hours'
          },
           {
            id:4,
            title:'54',
            info: 'Awards'
          }
  ]
 
  return (
    <div className='clients container'>
      {data.map(d=>
     <ClientsList key={d.id} active={selected === d.id} id={d.id} 
     title={d.title} info={d.info} 
     setSelected={setSelected} />
        )}
    </div>
  )
}

export default Clients