import './ClientsList.scss'


function ClientsList({id, title, info, active, setSelected}) {
  return (
      <div key={id} className={active? 'item active' : 'item'}
      onClick={()=>setSelected(id)}>
          <div className="inner">
          <h2>{title}</h2>
          <p>{info}</p>
          </div>
        
      </div>
  )
}

export default ClientsList