import './Blog.scss'

function Blog() {

const data =[
  {
    id:1,
    title: 'Is Windows Buggy?',
    author: 'David Larweh',
    article: `It is important to understand how the command-line processing occurs. The procedure
     of examining the command issued is not as straight-forward as you might think.`,
          img: 'https://cdn.pixabay.com/photo/2019/08/17/13/13/microsoft-4412148__340.jpg'
  },

  {
    id:2,
    title: 'You must learn Linux!',
    author: 'Ernest Thompson',
    article: `It is important to understand how the command-line processing occurs. The procedure
     of examining the command issued is not as straight-forward as you might think.`,
     img: 'https://cdn.pixabay.com/photo/2016/12/11/20/44/background-1900329__340.jpg'
  },

  {
    id:3,
    title: 'React, the future of Web Dev?',
    author: 'John Doe',
    article: `It is important to understand how the command-line processing occurs. The procedure
     of examining the command issued is not as straight-forward as you might think.`,
    img: 'data:image/png;base64,'
  },

]

  return (
    <div className='blog container' id='blog'>
     <div className="top">
        <h3>LATEST NEWS</h3>
        <h1 className="heading">My Blog</h1>
        <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. 
          Commodi obcaecati similique a ipsam beatae 
          saepe velit asperiores magnam aut quibusdam!</p>
      </div>
      <div className="bottom">
        
          {data.map(d=>
          <div key={d.id} className="item">
            <div className="image">
               <img src={d.img} alt="" />
            </div>
             <div className="title">
                <h3>{d.title}</h3>
                <h4>{d.author}</h4>
             </div>
             <p>{d.article}</p>
             <a href="#w">READ MORE</a>
           </div>
            )}
          
        
      </div>
    </div>
  )
}

export default Blog