import React from 'react'
import './About.scss'
import aboutDon from '../../Assets/about-don.png'
import pat from '../../Assets/pattern.png'

function About() {


  const skill = [
          {
            id:1,
            title: 'HTML/CSS'
          },
           {
            id:2,
            title: 'React.JS'
          },
           {
            id:3,
            title: 'Graphics'
          },
           {
            id:4,
            title: 'Wordpress'
          }
  ]
  
  return (
    <div className='about container'id='about'>
      <div className="top">
        <h3>WHO AM I</h3>
        <h1 className="heading">About me</h1>
      </div>
      <div className="bottom">
      <div className="left">
        <h2>Hello again, I'm</h2>
        <p>Lorem ipsum, dolor sit amet consectetur adipisicing elit. Aliquid 
          quia asperiores, ut accusamus tempora blanditiis! 
          Ratione laudantium magnam commodi nesciunt soluta nisi eveniet non
           distinctio, iusto obcaecati velit quae. Quasi!</p>
           <div className="bars">
            {skill.map(s=>
              <div key={s.id} className="bar">
               <p>{s.title}</p>
               <span></span>
             </div>
              )}
           </div>
           <a href="#w" className="global-btn">READ MORE</a>
      </div>
      <div className="right">
        <img src={aboutDon} alt="" />
      </div>

      </div>
      <img className='pat-left' src={pat} alt="" />
      <img className='pat-right' src={pat} alt="" />
    </div>
  )
}

export default About